import React from 'react';
import { Flex } from 'rebass';
import useSound from 'use-sound';

import FloatIn from '../../components/FloatIn';
import MuteButton from '../../components/MuteButton';

// Remember to remove this placeholder before deploying
import CoolSound from '../../assets/tsfh-aoal.mp3';

import thesisHeaderImg from './assets/header_smol.png';
import thesisWhiteTitleImg from './assets/title_white.png';
import thesisFrames from './assets/frames';
import thesisBigBlackTitle from './assets/big_black_title.png';

import useBooleanState from '../../hooks/useBooleanState';

import * as Styled from './styles';

const Thesis = () => {
  const [play, { sound, pause }] = useSound(CoolSound);
  const muted = useBooleanState();

  const pauseResume = () => {
    if (sound.playing()) {
      pause();
    } else {
      play();
    }
  };

  const toggleMute = () => {
    sound.mute(!muted.state);
    muted.toggle();
  };

  return (
    <Flex
      flexDirection="column"
      alignItems="center"
      alignContent="center"
      justifyContent="center"
      flex={1}
    >
      <MuteButton toggleMute={toggleMute} mutedState={muted.state} />
      <Styled.ThesisHeaderContainer>
        <Styled.ThesisHeader src={thesisHeaderImg} />
        <Styled.WhiteTitleContainer>
          <FloatIn>
            <Styled.ThesisWhiteTitle src={thesisWhiteTitleImg} style={{ zIndex: 42069 }} />
          </FloatIn>
        </Styled.WhiteTitleContainer>
      </Styled.ThesisHeaderContainer>

      <Styled.ImagesRow>
        <Styled.ThesisBigBlackTitle src={thesisBigBlackTitle} />
        <FloatIn key="thesis-frame-1" delay="500ms">
          <Styled.TestImg src={thesisFrames[0]} />
        </FloatIn>
        <FloatIn key="thesis-frame-2" delay="1000ms">
          <Styled.TestImg src={thesisFrames[1]} />
        </FloatIn>
        <FloatIn key="thesis-frame-3" delay="1500ms">
          <Styled.TestImg src={thesisFrames[2]} />
        </FloatIn>
      </Styled.ImagesRow>
      <Styled.ImagesRow>
        <FloatIn key="thesis-frame-4" delay="500ms">
          <Styled.TestImg src={thesisFrames[3]} />
        </FloatIn>
        <FloatIn key="thesis-frame-5" delay="1000ms">
          <Styled.TestImg src={thesisFrames[4]} />
        </FloatIn>
        <FloatIn key="thesis-frame-6" delay="1500ms">
          <Styled.TestImg src={thesisFrames[5]} />
        </FloatIn>
      </Styled.ImagesRow>
      <Styled.ImagesRow>
        <FloatIn key="thesis-frame-7" delay="500ms">
          <Styled.TestImg src={thesisFrames[6]} />
        </FloatIn>
        <Styled.Spacer />
        <Styled.Spacer />
      </Styled.ImagesRow>




      <Flex
        flex={1}
        alignItems="center"
        alignContent="center"
        justifyContent="center"
      >
        <FloatIn>
          <button onClick={pauseResume}>hey look, a button</button>
        </FloatIn>
        <FloatIn delay="800ms">
          <button onClick={toggleMute}>{muted.state ? 'Muted' : 'Unmuted'}</button>
        </FloatIn>
      </Flex>
    </Flex>
  );
};

export default Thesis;
